	module.exports = {"data":{"markdownRemark":{"html":"<p>Okay, it’s been a while. I’ve still been making art, just haven’t been updating the blog. I’ve been saying “yes” a lot lately, maybe too much.</p>\n<p>Here’s my latest portrait from last night’s class.</p>\n<p><img src=\"/2018-7-27-346c34c5d072eddc7bee8870cd6d749f.jpg\" alt=\"Profile with dramatic lighting\">\nI was sitting almost exactly opposite of the light source, so that was fun. Still getting better at being less representational and intentionally leaving artifacts which signify that this is a human’s attempt to represent something.</p>\n<p>And if you have a minute and a half to waste, you can watch <a href=\"https://vimeo.com/user87775051/review/282012329/c1e07dc4ad\">this video</a> of the creation process.</p>\n<p>I made another yet-to-be-posted portrait last week, but I’ll wait until tomorrow to post that.</p>\n<p>Tangentially related, but the space where the class is held is called Syzygy. The mastermind behind Syzygy was at the class last night talking about how his mission is to foster the local arts scene. He wants everyone to know that the scene in San Francisco is not dead! He plans to use the space to help emerging artists practice and gain exposure.</p>","excerpt":"Okay, it’s been a while. I’ve still been making art, just haven’t been updating the blog. I’ve been saying “yes” a lot lately, maybe too much. Here’s my latest portrait from last night’s class. \nI was sitting almost exactly opposite of the light…","frontmatter":{"date":"July 27, 2018","path":"/blog/2018/7/27","title":"New stuff","category":"life, the universe and everything"}}},"pathContext":{}}

